<template>
  <div>
    <div v-show="show" class="music">
      <div class="music__main">
        <div :class="['music__main__cover', isPlay ? '' : 'active']" @click="play_cd">
          <img src="/static/images/meet.png" />
        </div>
        <div class="music__main__timeBar">
          <span style="color: white">{{ audioSrc.title }}</span>
          <div class="time">
            <span>{{ realMusicTime }}</span>
            <span>{{ totalMusicTime }}</span>
          </div>
          <div class="bar" ref="bar" @click="handClickBar">
            <div class="bar__slid" ref="slid" @click="handClickBar"></div>
          </div>
        </div>
      </div>
      <div class="music_list_bottom"  v-if="music_list_show">
        <div class="music_list" v-for="item in audioSrcs">
          {{ item.title }}
      </div>
      </div>
      <div class="music__mask">
      </div>
      <audio ref="music" :src="audioSrc.url"></audio>
      <div class="music__btn">
        <i class="el-icon-arrow-left" @click="leftMusic"></i>
        <img src="/static/images/play.png" class='icon' @click="play" v-if="isPlay" />
        <img src="/static/images/pause.png" class='icon' @click="pause" v-if="!isPlay" />
        <i class="el-icon-arrow-right" @click="rightMusic"></i>
        <i class="el-icon-refresh" @click="switchMusic"></i>
        <i class="el-icon-arrow-down" @click="show_music"></i>
        <i class="el-icon-more" @click="show_music_list"></i>
      </div>
    </div>

    <div v-show="!show" class="music_cd">
      <div :class="['music__main__cover', isPlay ? '' : 'active']" @click="show_music">
        <img src="/static/images/meet.png" />
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    this.audioSrcs = [
      { 'title': '芒种', 'url': 'https://music.163.com/song/media/outer/url?id=1369798757.mp3' },
      { 'title': '向云端', 'url': 'https://music.163.com/song/media/outer/url?id=2049512697.mp3' },
      { 'title': '麒麟', 'url': 'https://music.163.com/song/media/outer/url?id=1974459985.mp3' },
      { 'title': '侧脸', 'url': 'https://music.163.com/song/media/outer/url?id=534542079.mp3' },
      { 'title': '迷失幻境', 'url': 'https://music.163.com/song/media/outer/url?id=1493758352.mp3' },
    ];
    return {
      show: true,
      music_list_show:false,
      isPlay: true,
      realMusicTime: "00:00",
      totalMusicTime: "00:00",
      musicIndex: 0,
      audioSrc: this.audioSrcs[0]
    };
  },
  created() {

  },
  mounted() {
    this.watchMusicTime();
  },
  methods: {
    show_music() {
      this.show = !this.show
      this.music_list_show=false
    },
    show_music_list(){
      this.music_list_show=!this.music_list_show
    },
    pause() {
      this.music.pause();
      this.isPlay = true;
    },
    play() {
      this.music.play();
      this.isPlay = false;
    },
    play_cd() {
      if (this.music.paused) {
        this.music.play();
        this.isPlay = false;
      } else {
        this.music.pause();
        this.isPlay = true;
      }
    },
    // 处理时间
    handlMusicTime() {
      //用秒数来显示当前播放进度
      let timeDisplay = Math.floor(this.music.currentTime); //获取实时时间
      //分钟
      let minute = parseInt(timeDisplay / 60);
      if (minute < 10) {
        minute = "0" + minute;
      }
      //秒
      let second = Math.round(timeDisplay % 60);
      if (second < 10) {
        second = "0" + second;
      }
      this.realMusicTime = minute + ":" + second;
    },
    // 处理进度条
    handMusicBar() {
      let slid = this.$refs.slid;
      let duration = this.music.duration;
      let x = ((this.music.currentTime / duration) * 100).toFixed(2) + "%";
      slid.style.width = x;
    },
    // 处理点击进度条事件
    handClickBar(e) {
      const barTotalWidth = this.bar.offsetWidth; // bar 总宽度
      const rect = e.target.getBoundingClientRect(); // 元素右边距离页面边距的距离 返回上下左右
      let length = e.pageX - rect.left;
      this.music.currentTime = length / barTotalWidth * this.music.duration; // 计算播放时间 位置百分比*总时间
      this.$nextTick(() => {
        this.music.play();
        this.isPlay = true;
      })
    },
    //上一首
    leftMusic() {
      this.musicIndex -= 1;
      if (this.musicIndex < 0) {
        this.musicIndex = this.audioSrcs.length - 1
      }
      this.audioSrc = this.audioSrcs[this.musicIndex];
      this.music.load()
      this.isPlay = false;
      // 文件下载完毕，如果不用等到全部下载完毕，可以用canplay事件
      this.music.addEventListener("canplaythrough", () => {
        this.music.play();
        this.isPlay = false;
      });
    },
    //下一首
    rightMusic() {
      this.musicIndex += 1;
      if (this.musicIndex > this.audioSrcs.length-1) {
        this.musicIndex = 0
      }
      this.audioSrc = this.audioSrcs[this.musicIndex];
      this.music.load()
      this.isPlay = false;
      // 文件下载完毕，如果不用等到全部下载完毕，可以用canplay事件
      this.music.addEventListener("canplaythrough", () => {
        this.music.play();
        this.isPlay = false;
      });
    },
    // 重播歌曲
    switchMusic() {
      this.audioSrc = this.audioSrcs[this.musicIndex];
      this.music.load()
      this.isPlay = false;
      // 文件下载完毕，如果不用等到全部下载完毕，可以用canplay事件
      this.music.addEventListener("canplaythrough", () => {
        this.music.play();
        this.isPlay = false;
      });
    },


    //使用事件监听方式捕捉事件
    watchMusicTime() {
      this.music = this.$refs.music;
      this.bar = this.$refs.bar;
      this.music.addEventListener(
        "timeupdate",
        () => {
          this.handlMusicTime();
          this.$nextTick(() => {
            this.handMusicBar();
          })
        },
        false
      );
      // 播放完毕
      this.music.addEventListener("ended", () => {
        this.rightMusic(); // 下一首
      });
      // 捕获音频文件已准备完毕
      // 当媒体文件可以播放的时候会触发oncanplay事件,也可以用oncanplay
      this.music.oncanplaythrough = () => {
        let time = this.music.duration;
        //分钟
        let minutes = parseInt(time / 60);
        if (minutes < 10) {
          minutes = "0" + minutes;
        }
        //秒
        let seconds = Math.round(time % 60);
        if (seconds < 10) {
          seconds = "0" + seconds;
        }
        this.totalMusicTime = minutes + ":" + seconds;
      };
    }
  }
};
</script>
<style lang="scss">
@keyframes musicRotate {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}
//歌单
.music_list_bottom{
  position: fixed;
  bottom: 130px;
  left: 0;
  text-align: center;
  padding: 10px;
  // background-image: url('/static/images/music.jpg');
  background-repeat: no-repeat;
  background-size: cover;
  background-color: rgba(150,160,210,0.7);
  border-radius: 20px;
  color: white;
}
.music_list{
  position: relative;
  left: 0;
  width: 280px;

}

.icon {
  position: relative;
  top: 3px;
  width: 20px;
  height: 20px;
}

.music_cd {
  position: fixed;
  z-index: 100;
  bottom: 0px;
  left: 0;

  .music_cd_img {
    width: 20px;
    height: 20px;
  }
}

.music {
  z-index: 100;
  width: 300px;
  height: 120px;
  bottom: 0px;
  margin: 0 auto;
  border-radius: 15px;
  position: fixed;
  left: 0;
  padding: 30px;
  box-sizing: border-box;
  overflow: hidden;

  &__main {
    display: flex;

    &__cover {
      width: 80px;
      height: 80px;
      overflow: hidden;
      border-radius: 50%;
      background-color: #dddddd;
      cursor: pointer;
      animation: musicRotate 10s linear infinite;
      animation-play-state: paused; // 暂定动画

      img {
        width: 100%;
        height: 100%;
      }

      &.active {
        animation-play-state: running; // 运行动画
      }
    }

    &__timeBar {
      flex: 1;
      display: flex;
      flex-direction: column;
      justify-content: space-evenly;
      padding-left: 20px;
      box-sizing: border-box;

      .time {
        display: flex;
        justify-content: space-between;
        color: #fff;

        span {
          font-size: 19px;
          line-height: 1;
        }
      }

      .bar {
        height: 8px;
        background-color: #fbfbfb;
        border-radius: 8px;
        position: relative;
        border-radius: 8px;
        overflow: hidden;
        cursor: pointer;

        &__slid {
          position: absolute;
          left: 0;
          top: 0;
          background-color: #e24d80;
          height: 100%;
          width: 0;
          transition: width 0.3s;
        }
      }
    }
  }

  &__btn {
    position: absolute;
    right: 30px;
    top: 10px;

    i {
      font-size: 18px;
      color: #fff;
      cursor: pointer;
    }
  }

  &__mask {
    background-image: url('/static/images/music.jpg');
    z-index: -2;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 50%;
    filter: blur(15px);
    opacity: 0.5;
    transition: all 0.8s;
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;

    &::before {
      position: absolute;
      top: 0;
      right: 0;
      left: 0;
      bottom: 0;
      z-index: -1;
      content: '';
      background-color: rgba(0, 0, 0, 0.08);
    }
  }
}
</style>




